.title {
  display: block;
  text-align: center;
  margin-top: 30px;
  font-size: 40px;
  font-weight: 800;
}
.btnList {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
button {
  width: 200px;
  padding: 15px;
  margin-right: 20px;
  background-color: rgba(182, 182, 182, 0.288);
  color: #000;
  font-size: 20px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
}
button:last-child {
  margin-right: 0;
}
.list {
  display: inline-block;
  margin-left: 300px;
  justify-content: center;
}
p {
  font-size: 20px;
  font-weight: 500;
}
